.custom-checkbox__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;

  &::before {
    transition: background $transition;
    margin-right: 15px;
    content: "";
    display: block;
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    border: 0;
    border-radius: 6px;
    background-color: var(--checkbox-default);
    background-repeat: no-repeat;
    background-size: 12px 12px;
    background-position: center;
  }

  &:hover {
    &::before {
      box-shadow: inset 0 0 0 1px var(--primary-color);
    }
  }
}

.custom-checkbox__label--white {
  &::before {
    background-color: #ffffff;
  }
}

.custom-checkbox__label--empty {
  &::before {
    margin-right: 0;
  }
}

.custom-checkbox__label--top {
  &::before {
    align-self: flex-start;
  }
}

.custom-checkbox__input:checked {
  ~ .custom-checkbox__label {
    &::before {
