    }
  }
}

.custom-checkbox__input:focus-visible {
  ~ .custom-checkbox__label {
    &::before {
      box-shadow: inset 0 0 0 2px var(--primary-color);
    }
  }
}

.custom-checkbox__input:checked:focus-visible {
  ~ .custom-checkbox__label {
    &::before {
      box-shadow: inset 0 0 0 2px var(--text-primary);
    }
  }
}
